// components/ListeSoutenances/SoutenanceTable.tsx
import React from "react";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import {
  Edit2,
  Users,
  Trash2,
  CalendarClock,
  CheckCircle2,
  Clock,
  XCircle,
  AlertCircle,
} from "lucide-react";
import {
  Tooltip,
  TooltipContent,
  TooltipProvider,
  TooltipTrigger,
} from "@/components/ui/tooltip";
import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
  AlertDialogTrigger,
} from "@/components/ui/alert-dialog";

interface Soutenance {
  id: number; // Aligned with your original ListeSoutenances interface
  date: string;
  time: string;
  location: string;
  groupe: string; // Aligned with 'groupe' from ListeSoutenances
  juryNames: string | string[];
  status: string;
}

interface SoutenanceTableProps {
  soutenances: Soutenance[];
  onEdit: (id: number) => void;
  onDelete?: (id: number) => void; // Optional delete handler
}

interface StatusConfig {
  color: string;
  bg: string;
  hoverBg: string;
  icon: React.ReactNode;
  text: string;
}

// StatusBadge Component (Inline)
function getStatusConfig(status: string): StatusConfig {
  switch (status) {
    case "Scheduled":
      return {
        color: "text-blue-700 dark:text-blue-300",
        bg: "bg-blue-100 dark:bg-blue-900/50",
        hoverBg: "hover:bg-blue-200 dark:hover:bg-blue-900/70",
        icon: <CalendarClock className="w-3.5 h-3.5" />,
        text: "Planifi√©",
      };
    case "Completed":
      return {
        color: "text-green-700 dark:text-green-300",
        bg: "bg-green-100 dark:bg-green-900/50",
        hoverBg: "hover:bg-green-200 dark:hover:bg-green-900/70",
        icon: <CheckCircle2 className="w-3.5 h-3.5" />,
        text: "Termin√©",
      };
    case "Pending":
      return {
        color: "text-yellow-700 dark:text-yellow-300",
        bg: "bg-yellow-100 dark:bg-yellow-900/50",
        hoverBg: "hover:bg-yellow-200 dark:hover:bg-yellow-900/70",
        icon: <Clock className="w-3.5 h-3.5" />,
        text: "En attente",
      };
    case "Cancelled":
      return {
        color: "text-red-700 dark:text-red-300",
        bg: "bg-red-100 dark:bg-red-900/50",
        hoverBg: "hover:bg-red-200 dark:hover:bg-red-900/70",
        icon: <XCircle className="w-3.5 h-3.5" />,
        text: "Annul√©",
      };
    default:
      return {
        color: "text-gray-700 dark:text-gray-300",
        bg: "bg-gray-100 dark:bg-gray-800",
        hoverBg: "hover:bg-gray-200 dark:hover:bg-gray-800/70",
        icon: <AlertCircle className="w-3.5 h-3.5" />,
        text: "Inconnu",
      };
  }
}

function StatusBadge({ status }: { status: string }) {
  const config = getStatusConfig(status);

  return (
    <Badge
      variant="outline"
      className={`
        ${config.bg} 
        ${config.color} 
        ${config.hoverBg}
        border-0 
        font-medium 
        gap-1.5
        px-2.5 
        py-0.5 
        transition-colors
        duration-200
        inline-flex
        items-center
        cursor-default
        select-none
        shadow-sm
      `}
    >
      {config.icon}
      {config.text}
    </Badge>
  );
}

// DeleteDialog Component (Inline)
function DeleteDialog({
  onDelete,
  Idsoutenance,
}: {
  onDelete: () => void;
  Idsoutenance: number;
}) {
  return (
    <AlertDialog>
      <AlertDialogTrigger asChild>
        <Button
          variant="ghost"
          size="icon"
          className="hover:bg-red-100 hover:text-red-600 dark:hover:bg-red-900/30"
        >
          <Trash2 className="h-4 w-4" />
          <span className="sr-only">Supprimer la soutenance</span>
        </Button>
      </AlertDialogTrigger>
      <AlertDialogContent>
        <AlertDialogHeader>
          <AlertDialogTitle>Confirmer la suppression</AlertDialogTitle>
          <AlertDialogDescription>
            √ätes-vous s√ªr de vouloir supprimer cette soutenance ? Cette action est irr√©versible.
          </AlertDialogDescription>
        </AlertDialogHeader>
        <AlertDialogFooter>
          <AlertDialogCancel>Annuler</AlertDialogCancel>
          <AlertDialogAction
            onClick={onDelete}
            className="bg-red-600 hover:bg-red-700 dark:hover:bg-red-700"
          >
            Supprimer
          </AlertDialogAction>
        </AlertDialogFooter>
      </AlertDialogContent>
    </AlertDialog>
  );
}

// Main SoutenanceTable Component
export default function SoutenanceTable({
  soutenances,
  onEdit,
  onDelete,
}: SoutenanceTableProps) {
  return (
    <Table>
      <TableHeader className="bg-muted/50">
        <TableRow>
          <TableHead className="w-[250px] font-semibold text-lg">Date</TableHead>
          <TableHead className="w-[150px] font-semibold text-lg">Heure</TableHead>
          <TableHead className="w-[200px] font-semibold text-lg">Lieu</TableHead>
          <TableHead className="w-[150px] font-semibold text-lg">Groupe</TableHead>
          <TableHead className="w-[300px] font-semibold text-lg">Jury</TableHead>
          <TableHead className="w-[150px] font-semibold text-lg">Statut</TableHead>
          <TableHead className="w-[150px] font-semibold text-lg">Actions</TableHead>
        </TableRow>
      </TableHeader>
      <TableBody>
        {soutenances.map((soutenance) => (
          <TableRow
            key={soutenance.id}
            className="transition-colors hover:bg-muted/50"
          >
            <TableCell className="font-medium text-lg">
              {new Date(soutenance.date).toLocaleDateString("fr-FR", {
                day: "numeric",
                month: "long",
                year: "numeric",
              })}
            </TableCell>
            <TableCell className="text-lg">{soutenance.time}</TableCell>
            <TableCell className="text-lg">{soutenance.location}</TableCell>
            <TableCell>
              <Badge variant="secondary" className="font-medium text-lg">
                {soutenance.groupe}
              </Badge>
            </TableCell>
            <TableCell>
              <TooltipProvider>
                <Tooltip>
                  <TooltipTrigger asChild>
                    <div className="flex items-center gap-2 cursor-pointer">
                      <Users className="h-5 w-5 text-muted-foreground" />
                      <span className="truncate max-w-[250px] text-lg">
                        {Array.isArray(soutenance.juryNames)
                          ? soutenance.juryNames.join(", ")
                          : soutenance.juryNames}
                      </span>
                    </div>
                  </TooltipTrigger>
                  <TooltipContent>
                    <p className="max-w-xs text-lg">
                      {Array.isArray(soutenance.juryNames)
                        ? soutenance.juryNames.join(", ")
                        : soutenance.juryNames}
                    </p>
                  </TooltipContent>
                </Tooltip>
              </TooltipProvider>
            </TableCell>
            <TableCell>
              <StatusBadge status={soutenance.status} />
            </TableCell>
            <TableCell>
              <div className="flex items-center gap-2">
                <TooltipProvider>
                  <Tooltip>
                    <TooltipTrigger asChild>
                      <Button
                        variant="ghost"
                        size="icon"
                        className="hover:bg-muted"
                        onClick={() => onEdit(soutenance.id)}
                      >
                        <Edit2 className="h-5 w-5" />
                        <span className="sr-only">Modifier la soutenance</span>
                      </Button>
                    </TooltipTrigger>
                    <TooltipContent>
                      <p className="text-lg">Modifier la soutenance</p>
                    </TooltipContent>
                  </Tooltip>
                </TooltipProvider>
                {onDelete && (
                  <TooltipProvider>
                    <Tooltip>
                      <TooltipTrigger asChild>
                        <DeleteDialog
                          onDelete={() => onDelete(soutenance.id)}
                          Idsoutenance={soutenance.id}
                        />
                      </TooltipTrigger>
                      <TooltipContent>
                        <p className="text-lg">Supprimer la soutenance</p>
                      </TooltipContent>
                    </Tooltip>
                  </TooltipProvider>
                )}
              </div>
            </TableCell>
          </TableRow>
        ))}
      </TableBody>
    </Table>
  );
}